/**
 * Created by fuzl on 2017-11-9.
 * 路由配置，可以建多个路由配置文件，在不同页面中引用
 */
import List from "../views/list.vue"
import Menu from "../views/menu.vue"


export default [
	{path:"/",redirect:"menu"},
	{
		path: "/list",
		component: List,
		name: "list"
	},
	{
		path:"/menu",
		component:Menu,
		name:"menu"
	},


]


