<template>
    <div id="head" >
        <p @click="back"><span>{{this.in}}</span></p>
    </div>
</template>
<script>
export default {
	data(){
		return{

		}   
	},
	methods:{
        back(){
            this.$router.push('/menu')
        }
	},
    computed:{
	    in(){
	        return this.$store.state.name==""? "终极菜单":this.$store.state.name
        }
    }
}
</script>
<style lang='less'>
#head{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #409eff;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 20px;
    p{
        cursor:pointer;
        span{
            margin-left: 10px ;
        }
    }
}
</style>



