<template>
    <div id="menu">
        <div class="search">
            <el-input ref="test" v-model="name" placeholder="看看你喜欢的餐厅"></el-input>
            <el-button @click="search" >搜索</el-button>
            <el-button @click="reset">重置</el-button>
        </div>
        <ul>
            <li v-for="(item,index) in dataList" @click="toList(index,item.name)">
                <img :src="item.src">
                <div class="nav" >
                    <p class="title">{{item.name}}</p>
                    <p class="content">地址：{{item.content}}</p>
                    <p class="list">特色食物：{{item.list}}</p>
                    <p class="value">人均：￥{{item.value}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default{
	data(){
		return{
		    searchList:[],//搜索结果列表
		    name:"",//餐厅名称
		    list:[
                {name:"古早永乐村",src:require("../pic/guzao.jpg"),id:"gz",value:24,list:"油条，豆浆，烧饼，血糯米肉糟饭团",content:"文一路铂澜大象城2楼 电话： 0571-56928951"},
                {name:"喜豚亚洲猪排专门店",src:require("../pic/xitun.jpg"),id:"xt",value:60,list:"黑豚猪肉盖饭 猪排套餐 明虾配梅肉芝士套餐",content:"大关丽水路58号远洋乐堤港负一楼A012号"},
                {name:"Thai Square 泰渡泰国料理",src:require("../pic/taidu.jpg"),id:"td",value:137,list:"网红咖喱蟹，大虾冬阴功,菠萝海鲜饭,碳烤猪颈肉,虾饼,芒果糯米饭",content:"高新文教区 教工路26世贸丽晶城欧美中心D座二楼"},
                {name:"德吉蒙古煨炉餐厅  ",src:require("../pic/deji.png"),id:"dj",value:113,list:"烤羊排,煨炉烤羊腿,酸奶,羊肉串,羊肉烧卖,羊杂汤,一品蔬菜锅",content:"古墩路沿线 古墩路385号-13"},
                {name:"留下来筒骨砂锅(益乐路店)",src:require("../pic/liuxialai.jpg"),id:"lxl",value:78,list:"筒骨煲,招牌虾滑,自助水果,深海虾,砂锅大王,小油条, 非一般的山药 ",content:"高新文教区 益乐路43号"},
                {name:"辣艺金牌香辣蟹",src:require("../pic/layi.jpg"),id:"ly",value:85,list:"香辣蟹捉海鲜,金牌香辣蟹,招牌肉蟹煲,炝拌木耳,厥根粉",content:"西溪 余杭塘路与崇仁路交叉口西溪银泰城4楼F4006"},
                {name:"塔哈尔新疆盛宴(城西银泰城店)",src:require("../pic/tahaer.jpg"),id:"the",value:86,list:"羊肉串,自制酸奶,沙湾大盘鸡,新疆奶茶,买苏拉提烤羊排",content:"城西银泰 丰潭路380号城西银泰城3F033号"},
                {name:"京韵怡园北京菜",src:require("../pic/jingyun.jpg"),id:"jy",value:84,list:"北京烤鸭/半只,小吊梨汤,香辣炭烤牛蛙,京酱肉丝,京韵豆腐,传统豌豆黄 ",content:"西溪 双龙街与崇仁路交叉口杭州西溪银泰城4楼京韵怡园"},
            ],
		}
	},
	methods:{
        toList(id,name){
            // console.log(id)
            this.$store.state.id=id;
            this.$store.state.name=name;
            this.$router.push("/list");
        },
        search(){
            if(this.name==""){
                alert("请输入您要查询的食物！","提示")
                this.searchList=[]
            }else {
                this.searchList=[]
                let name=this.name.split("")
                for(let i=0;i<name.length;i++){
                    for(let j=0;j<this.list.length;j++){
                        if(this.list[j].name.split("").indexOf(name[i])>=0){
                            if(this.searchList.indexOf(this.list[j])<0){
                                this.searchList.push(this.list[j])
                            }
                        }
                    }
                }
                if(this.searchList.length==0){
                    alert("无此食物请重新搜索!","提示")
                }
            }
        },
        reset(){
            this.searchList=[]
            this.name=""
        }

	},
	mounted(){
        this.$store.state.id=null;
        this.$store.state.name="";
	},
    computed:{
	    dataList(){
            return this.searchList.length==0 ? this.list : this.searchList
        }
    }
}


</script>
<style lang='less'>
#menu{
    .search{
        width: 100%;
        height: 26px;
        margin:4px 0;
        .el-input{
            width: 56%;
            float: left;
            margin-left:2%;
        }
        .el-button{
            width: 20%;
            float: left;
            margin-left: 1%;
        }
    }
    ul{
        list-style-type: none;
        padding: 0;
        margin: 0;
        li{
            cursor: pointer;
            width: 100%;
            height: 100px;
            margin: 20px 0 ;
            overflow: hidden;
            background: #f3f5f7;
            img{
                width: 25%;
                height: 100px;
                float: left;
            }
            .nav{
                height: 100px;
                width: 280px;
                float: left;
                .title{
                    padding-left: 12px;
                    color: #000;
                    font-weight: bold;
                }
                .content{
                    padding:0px 12px;
                    font-size: 12px;
                    color: #999;
                }
                .list{
                    padding:0px 12px;
                    font-size: 12px;
                    color: #999;
                }
                .value{
                    color: red;
                    padding-left: 12px;
                    font-size: 12px;
                }
            }
        }
    }
}
</style>


