<template>
    <div id="goodsSearch">
		<div class="search">
            <el-input ref="test" v-model="food" placeholder="请输入要搜索的食物" @keyup.native="show($event)"></el-input>
            <el-button @click="search" >搜索</el-button>
			<el-button @click="reset">重置</el-button>
		</div>
		<ul>
			<li v-for="(item,index) in list">
				<img :src="source(index)">

				<div class="nav">
					<p class="title">{{index+1}}.{{item.name}}</p>
					<p class="content">{{item.content}}</p>
					<p class="value">￥{{item.value}}</p>
				</div>
				</li>
		</ul>
	</div>
</template>
<script>
export default{
	data(){
		return{
			food:"",
			searchList:[]
		}
	},
	methods:{
		show(ev){
			if(ev.keyCode == 13){
				this.search()
			}
		},
		search(){
			// console.log(this.food);
			if(this.food==""){
			    alert("请输入您要查询的食物！","提示")
				this.searchList=[]
			}else {
				this.searchList=[]
				let food=this.food.split("")
				// console.log(food)
				for(let i=0;i<food.length;i++){
					for(let j=0;j<this.$store.state.goods[this.$store.state.id].length;j++){
						if(this.$store.state.goods[this.$store.state.id][j].name.split("").indexOf(food[i])>=0){
							if(this.searchList.indexOf(this.$store.state.goods[this.$store.state.id][j])<0){
								this.searchList.push(this.$store.state.goods[this.$store.state.id][j])
							}
						}
					}
				}
				if(this.searchList.length==0){
					alert("无此食物请重新搜索!","提示")
				}
			}
			//console.log(this.searchList);
		},
		reset(){
		    this.searchList=[]
		    this.food=""
		},
        source(index){
            // console.log(index)
            return (require('../pic/'+this.$store.state.id+'/'+index+'.jpg'))
        }
	},
	computed:{
		list(){
		    return this.searchList.length==0 ? this.$store.state.goods[this.$store.state.id] : this.searchList
		},

	},
	mounted(){
		console.log(this.$store.state.id)
	}
}
</script>
<style lang='less'>
#goodsSearch{
	.search{
		 width: 100%;
		 height: 26px;
        margin:4px 0;
        .el-input{
            width: 56%;
            float: left;
			margin-left:2%;
        }
        .el-button{
            width: 20%;
            float: left;
			margin-left: 1%;
        }
	}
	ul{
		list-style-type: none;
		padding: 0;
		margin: 0;
		li{
			width: 100%;
			height: 80px;
			margin: 20px 0 ;
			overflow: hidden;
			background: #f3f5f7;
			img{
				width: 80px;
				height: 80px;
				float: left;
			}
			.nav{
				height: 100px;
				width: 170px;
				float: left;
				.title{
					padding-left: 12px;
					color: #000;
				}
				.content{
					padding:10px 12px;
					font-size: 12px;
					color: #999;
				}
				.value{
					color: red;
					padding-left: 12px;
					font-size: 16px;
				}
			}
			}
		}
	}

</style>


